  #txt {
    color: black;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #ef1111;
    text-decoration: underline;
  }
  .hand {
    transform: rotate(0deg);
    animation: floater 1.5s infinite;
    font-size: 24px;
    margin-left: 2%;
  }
  @keyframes floater {
    0% {
      transform: rotate(0deg);
      transition: ease 0.5s;
    }
    50% {
      transform: rotate(15deg);
      transition: ease 0.5s;
    }
  }
  